<template>
  <div class="edit-info">
    <h2 class="title">
      {{$route.name == '修改公号' ? '编辑公号信息' : '接管新公号'}}
    </h2>
    <Form :model="formData" class="form-group" label-position="right" :label-width="100">
      <FormItem label="公号名称" prop="name">
        <Input size="large" type="text" v-model="formData.name" placeholder="请输入公号名称"></Input>
      </FormItem>
      <FormItem label="appid" prop="appid">
        <Input size="large" type="text" v-model.trim="formData.appid" placeholder="请输入appid"></Input>
      </FormItem>
      <FormItem label="appsecret" prop="appsecret">
        <Input size="large" type="text" v-model.trim="formData.appsecret" placeholder="请输入appsecret"></Input>
      </FormItem>
      <FormItem label="token" prop="token">
        <Input size="large" type="text" v-model="formData.token" placeholder="请输入token"></Input>
      </FormItem>
      <FormItem v-show="show" label="是否实时获取用户信息" prop="real_time">
        <i-switch v-model="formData.real_time"/>
      </FormItem>
      <FormItem label="是否保存用户" prop="save_user">
        <i-switch v-model="formData.save_user"/>
      </FormItem>
      <FormItem label="选择标签">
        <Tag v-if="changeTag" :name="formData.group" closable @on-close="handleClose">{{formData.group}}</Tag>
        <Input v-else size="large" type="text" v-model="formData.group" placeholder="请输入分组名"
               @on-blur="tagConfirm"></Input>
      </FormItem>
      <FormItem label="标签选项">
        <Tag v-for="(item, index) in tagList" :name="item.name" @click.native="select(item)" closable
             @on-close="deleteTag(item, index)">{{item.name}}
        </Tag>
      </FormItem>
      <FormItem label="公号属性">
        <RadioGroup v-model="formData.attribute">
          <Radio label="0">默认</Radio>
          <Radio label="1">男</Radio>
          <Radio label="2">女</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem>
        <Button class="edit-btn" size="large" type="primary" @click="submitForm">提交</Button>
        <Button class="edit-btn" size="large" type="error" @click="resetForm">重置</Button>
        <Button class="edit-btn" size="large" type="warning" @click="cancel">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        formData: {
          token: "mingxingshuo",
          real_time: false,
          save_user: false,
          attribute: '0'
        },
        tagList: [],
        changeTag: false,
        show: true
      };
    },
    mounted() {
      this.showTagList()
      if (this.$route.name === "修改公号") {
        this.formData = this.$route.query;
        this.formData.attribute = this.formData.attribute.toString();
      }
    },
    methods: {
      // trim() {
      //   this.formData.appid = this.formData.appid === '' ? this.formData.appid : this.formData.appid.trim()
      //   this.formData.appsecret = this.formData.appsecret === '' ? this.formData.appsecret : this.formData.appsecret.trim()
      // },
      cancel() {
        this.$router.push("/manage/gonghao/tuoguan");
        this.resetForm()
      },
      resetForm() {
        this.formData = {
          token: "mingxingshuo",
          real_time: false,
          save_user: false,
          attribute: '0'
        };
      },
      submitForm() {
        if (this.$route.path === "/manage/gonghao/tuoguan/create") {
          this.$axios.post("/conf/create", this.formData).then(res => {
            window.location.href = "/manage/gonghao/tuoguan";
            this.$Message.info("新建成功");
            this.resetForm()
          });
        } else if (this.$route.path === "/manage/gonghao/tuoguan/update") {
          this.$axios.post("/conf/update", this.formData).then(res => {
            window.location.href = "/manage/gonghao/tuoguan";
            this.$Message.info("修改成功");
            this.resetForm()
          });
        }
      },
      showTagList() {
        this.$axios.get("/gonghaoTag").then(res => {
          this.tagList = res.data.data;
        });
      },
      tagConfirm() {
        this.changeTag = true;
        this.$axios.post("/gonghaoTag", {name: this.formData.group})
            .then(res => {
              if (res.data.exist == 0) {
                this.tagList.push(res.data.data)
              }
            })
      },
      handleClose() {
        this.formData.group = "";
        this.changeTag = false
      },
      select(item) {
        this.formData.group = item.name;
        this.changeTag = true
      },
      deleteTag(item, index) {
        this.$Modal.confirm({
          title: '提示:',
          content: '确认删除这个分组吗？',
          onOk: () => {
            this.$axios.delete('/gonghaoTag/' + item._id)
                .then(res => {
                  this.tagList.splice(1, index)
                });
          }
        })
      }
    }
  };
</script>

<style scoped>
  .edit-info {
    width: 500px;
    margin: auto;
    text-align: left;
  }

  .form-group {
    width: 500px;
  }

  .title {
    margin-bottom: 30px;
  }

  .edit-btn {
    margin-right: 10px;
  }
</style>
